import LearningMode from "../LearningMode";
import LearnMenuModes from "./LearnMenuModes";
import { faVial } from "@fortawesome/free-solid-svg-icons";

export default class LearnKanaMode extends LearnMenuModes {
    getLearningTopics(): LearningMode[] {
        return [
            new LearningMode("Hiragana", "#fdb40e", "", { kana: { hiragana: true } }),
            new LearningMode("Katakana", "#ff7730", "", { kana: { katakana: true } }),
            new LearningMode("Diacriticals", "#1785e2", "", { kana: { diacriticals: true } }),
            new LearningMode("Diagraphs", "#a01219", "", { kana: { diagraphs: true } }),
            new LearningMode("All", "#fc3131", "", { kana: { hiragana: true, katakana: true, diagraphs: true, diacriticals: true } }),
            new LearningMode("Custom", "#41d085", faVial, {})
        ];
    }

    getTopic(): string {
        return "KANA"
    }
}